import { fahrenheitToCelsius } from "../utils/convert-temp";
import { celsiusToFahrenheit } from "../utils/convert-temp";

export function modifyTempData(weatherPromise, tempScale) {
  return weatherPromise.then((weatherData) => {
    switch (tempScale) {
      case "fahrenheit":
        weatherData.temp = fahrenheitToCelsius(weatherData.temp);
        weatherData.tempMax = fahrenheitToCelsius(weatherData.tempMax);
        weatherData.tempMin = fahrenheitToCelsius(weatherData.tempMin);
        weatherData.feelsLikeTemp = fahrenheitToCelsius(
          weatherData.feelsLikeTemp,
        );
        break;
      case "celsius":
        weatherData.temp = celsiusToFahrenheit(weatherData.temp);
        weatherData.tempMax = celsiusToFahrenheit(weatherData.tempMax);
        weatherData.tempMin = celsiusToFahrenheit(weatherData.tempMin);
        weatherData.feelsLikeTemp = celsiusToFahrenheit(
          weatherData.feelsLikeTemp,
        );
        break;
      default:
        console.log("Error with temperature scale inputted");
    }
    return weatherData;
  });
}
export async function getWeatherIcon(weatherData) {
  const condition = weatherData.icon;

  try {
    const icon = await import(`../weather-icons/${condition}.svg`);
    return icon.default;
  } catch (error) {
    console.error(`Icon for ${condition} not found`, error);
  }
}
