___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --orange: #f25c05;
  --white: #f2f0e4;
  --orange-dark: #ae4000;
  font-family: Inter, sans-serif;
  font-feature-settings:
    "liga" 1,
    "calt" 1; /* fix for Chrome */
}

@supports (font-variation-settings: normal) {
  :root {
    font-family: InterVariable, sans-serif;
  }
}
input {
  appearance: none;
}
ul {
  list-style-type: none;
}
button {
  font-family: inherit;
  color: inherit;
  background: none;
  border: none;
  cursor: pointer;
}
html {
  height: 100%;
}
body {
  min-height: 100vh;
  color: var(--white);
}
header {
  display: flex;
  padding: 20px;
  justify-content: space-between;
  background-color: var(--orange);
}
#search-city-form {
  display: flex;
  flex-direction: column;
  flex: 2;
  padding: 0px 80px;
  text-align: center;
  gap: 10px;

  & #search-bar {
    font-size: inherit;
    height: 3em;
    border-radius: 10px;
    outline: 0;
    border: none;
    padding: 10px;
  }
}
.dropdown {
  position: relative;

  & .dropdown-btn {
    background-color: var(--white);
    color: var(--orange);
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 1.2em;
  }
  & .dropdown-btn:hover {
    color: var(--white);
    background-color: var(--orange-dark);
  }
  & .dropdown-content {
    display: none;
    & ul {
      display: flex;
      flex-direction: column;

      position: absolute;
      background: var(--white);
      color: var(--orange);
      min-width: 175px;
      border-radius: 3px;
      top: 57px;
      right: 45px;
    }
    & li {
      padding: 5px;
      border-radius: 3px;
    }
    & li:hover {
      color: var(--white);
      background-color: var(--orange-dark);
      cursor: pointer;
    }
    & button {
      font-size: 1.1rem;
      padding: 5px;
      width: 100%;
      text-align: start;
    }
  }
  & .dropdown-content.visible {
    display: block;
  }
}
main {
  display: none;
  margin: 20px;
}
main.visible {
  display: flex;
  justify-content: space-evenly;
}
.info-card {
  height: 550px;
  padding: 20px;
  width: 45%;
  border-radius: 10px;
  background-color: var(--orange);
}
.data-with-unit {
  display: flex;
  align-items: start;
  gap: 5px;
}
#weather-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(5, 1fr);

  & > * {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
  }
  & #address {
    grid-column: 1 / 3;
    font-size: 3.5rem;
  }
  & #conditions {
    grid-column: 1 / 3;
  }
  & .weather-icon,
  #temp {
    grid-row: 2 / 4;
  }
  & #temp {
    font-size: 5rem;
  }
}
#weather-details-card {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);

  & section {
    display: flex;
    flex-direction: column;
    align-items: center;
    & h3 {
      flex: 1;
    }
    & p {
      flex: 2;
      font-size: 2.5rem;
    }
    & .data-with-unit {
      flex: 2;
    }
  }
}
